import React from "react";
import "./searchBar.css";



class SearchBar extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      term: '',
      location: '',
      sortBy: 'best_match'
    };

    this.handleTermChange = this.handleTermChange.bind(this);
    this.handleLocationChange = this.handleLocationChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);

     this.sortByOptions = {
      'Best Match': 'best_match',
      'Highest Rated': 'rating',
      'Most Reviewed': 'review_count'
    };
  }

  getSortByClass(sortByOption){
    if(this.state.sortBy == sortByOption){
      return 'active'
    }else{
      return ''
    }
      // this.state.sortBy == sortByOption ? 'active' : ''   
  }

 async handleSortByChange(sortByOption, event){
   await this.setState({
        sortBy: sortByOption
    });
    this.handleSearch(event);
  }

  handleTermChange(event){
      this.setState({
        term: event.target.value
      })
  }

  handleLocationChange(event){
      this.setState({
        location: event.target.value
      });
  }

  handleKeyDown(event){
    if (event.key === 'Enter') {
      this.handleSearch(event);
    }

  }

  handleSearch(event){
    this.props.searchYelp(this.state.term, this.state.location, this.state.sortBy);
    event.preventDefault();
  }


  renderSortByOptions() {
    return Object.keys(this.sortByOptions).map(sortByOption => {
      let sortByOptionValue = this.sortByOptions[sortByOption];
      return <li onClick={this.handleSortByChange.bind(this, sortByOptionValue)}
      className={this.getSortByClass(sortByOptionValue)} 
      key={sortByOptionValue}>{sortByOption}</li>;
    });
  }

  render() {
    return (
      <div className="SearchBar">
        <div className="SearchBar-sort-options">
          <ul>
             {this.renderSortByOptions()}
          </ul>
        </div>
        <div className="SearchBar-fields">
          <input onChange={this.handleTermChange} value={this.state.term} placeholder="Search Businesses" />
          <input onChange={this.handleLocationChange} value={this.state.location} placeholder="Where?" onKeyDown={this.handleKeyDown} />
        </div>
        <div onClick={this.handleSearch}  className="SearchBar-submit">
          <a>Let's Go</a>
        </div>
      </div>
    );
  }
}

export default SearchBar;